@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:800);
@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil);
/* refresh button styling*/

.button-refresh{
  background-color: #22252A;
  border-radius: 10px;
  border: none;
  text-decoration: none;
  outline:none;
  cursor:pointer;
  position: fixed;
  right:20px;
  top:38%;
  height: 80px;
}

/* refresh button on mouse over effect */
.button-refresh:hover{
  cursor:hand;
  background-color: #261447;
}

/*Setting up the display for the background image*/
.refreshed-image{
  position:absolute;
  top:0;
  left:0;
  min-width: 100%;
  min-height: 100%;
  width:100%;
  height: 100%;
  z-index: -2;
}

/*styling for the this+that text combination and rotating box*/

@-webkit-keyframes tipsy {
  0 {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0deg);
            transform: translateX(-50%) translateY(-50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(360deg);
            transform: translateX(-50%) translateY(-50%) rotate(360deg);
  }
}

@keyframes tipsy {
  0 {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0deg);
            transform: translateX(-50%) translateY(-50%) rotate(0deg);
  }
  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(360deg);
            transform: translateX(-50%) translateY(-50%) rotate(360deg);
  }
}

.box-this-that {
  text-align: center;
  font-family: 'Allerta Stencil', sans-serif;
  color: #fffbf1;
  text-shadow: 0 20px 25px #2e2e31, 0 40px 60px #00000000;
  font-size: 45px;
  font-weight: bold;
  text-decoration: none;
  letter-spacing: -3px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  max-width: 300px;
  line-height: 1.6;

}

.box-this-that:before,
.box-this-that:after {
  content: '';
  padding: .9em .4em;
  position: absolute;
  left: 50%;
  width: 100%;
  top: 50%;
  display: block;
  border: 15px solid red;
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(0deg);
          transform: translateX(-50%) translateY(-50%) rotate(0deg);
  -webkit-animation: 10s infinite alternate ease-in-out tipsy;
          animation: 10s infinite alternate ease-in-out tipsy;
}

.box-this-that:before {
  border-color: #322850 #322850 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  z-index: -1;
}

.box-this-that:after {
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #322850 #322850;
  box-shadow: 25px 25px 25px #00000000;
}

/* Main styles */

.text {
  font: 3em/1 Open Sans, Impact;
  text-transform: lowercase;
  fill: none;
  stroke-width: 4;
  stroke-linejoin: round;
  stroke-dasharray: 70 330;
  stroke-dashoffset: 0;
  -webkit-animation: stroke 6s infinite linear;
  animation: stroke 6s infinite linear;
}

.text:nth-child(5n + 1) {
  stroke: #f2385a;
  -webkit-animation-delay: -1.2s;
  animation-delay: -1.2s;
}
.text:nth-child(5n + 2) {
  stroke: #f5a503;
  -webkit-animation-delay: -2.4s;
  animation-delay: -2.4s;
}

.text:nth-child(5n + 3) {
  stroke: #e9f1df;
  -webkit-animation-delay: -3.6s;
  animation-delay: -3.6s;
}

.text:nth-child(5n + 4) {
  stroke: #56d9cd;
  -webkit-animation-delay: -4.8s;
  animation-delay: -4.8s;
}

.text:nth-child(5n + 5) {
  stroke: #3aa1bf;
  -webkit-animation-delay: -6s;
  animation-delay: -6s;
}

@-webkit-keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}


/* setting up display for particle effect*/

.particles-display{
  position: absolute;
  top:0;
  left:0;
  min-height:100%;
  min-width:100%;
  width:100%;
  height: 100vh;
  z-index: -1;
}

/*setting cursor for floating button on the right bottom corner*/
.floating-button *{
  outline: none !important;
  cursor:pointer;
}
.floating-button :hover{
  cursor: hand;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 32px;
  left:20px;;
  top: 40%;
  background-color: #22252A;
  padding: 10px;
  border-radius: 10px;
  color:white;
  cursor:pointer;
  height: 32px;
}
.bm-burger-button:hover{
  cursor:hand;
  background-color: #261447;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #111;
}

/* General sidebar styles */
.bm-menu {
  background-color: rgba(255,255,255,0.6);
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  margin-left:-10px;
  margin-top: -10px;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: rgba(255,255,255,0.9);
  margin-left:-10px;
  margin-top: -10px;
}

/* Wrapper for item list */
.bm-item-list {
  margin-top: 2em;
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

/*subtext styling*/

.text {
	font-family: "Allerta Stencil", sans-serif;
	letter-spacing: 1px;
}

.sub-data {
	background-image: url('https://media.giphy.com/media/3ohhwEZqxzKbNUHzji/giphy.gif');
	color: transparent;
	-moz-background-clip: text;
	-webkit-background-clip: text;
	text-transform: uppercase;
	font-size: 45px;
	margin-top: 1em;
	line-height: 1.0;
}

